#side-drawer {
  background: black;
  -webkit-box-shadow: 5px 5px 10px rgba(23, 23, 23, 0.89);
          box-shadow: 5px 5px 10px rgba(23, 23, 23, 0.89);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 300px;
  margin-top: 60px;
  z-index: 200;
}


/*--------------------------- Catagory Selector ------------------------*/

#drop-down {
  width: 100%;
  text-align: center;
  z-index: 999;
}

#drop-down button {
  width: 98.5%;
  height: 48px;
  padding: 0 20px;
  background: rgba(0, 125, 255, 0.75);
  color: white;
  border: none;
  font-size: 1.3rem;
  -webkit-box-shadow: inset 0.5px 0 7px #000;
          box-shadow: inset 0.5px 0 7px #000;
}

#drop-down button:focus,
#drop-down button:hover,
#drop-down button:active {
  background: rgb(0, 125, 255);
  height: 48px;
}

#dropdown-nav {
  margin: 0;
  width: 100%;
  display: none;
}

#topic-list {
  width: 100%;
  margin: 0;
  padding: 0;
  z-index: 999;
  background-color: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  position: absolute;
}

.topic {
  position: relative;
  width: 49%;
  min-width: 48px;
  padding: 20px 0;
  -ms-flex-pack: center;
      justify-content: center;
  text-decoration: none;
  list-style: none;
  color: white;
  background-color: rgba(0, 125, 255, 0.75);
  -webkit-box-shadow: 5px 5px 50px rgba(23, 23, 23, 0.89);
          box-shadow: 5px 5px 50px rgba(23, 23, 23, 0.89);
  border: 1px solid black;
  border-radius: 5px;
}

.topic:focus,
.topic:hover,
.topic:active {
  background: rgb(0, 125, 255);
}

/* --------------------------- Filter ------------------------------ */

form {
  text-align: center;
}

input[type="text"] {
  height: 34px;
  width: 98.5%;
  font-weight: bold;
  font-family: Verdana;
  -webkit-box-shadow: 0.5px 0 7px #000;
          box-shadow: 0.5px 0 7px #000;
  border: 1px solid black;
}

input[type="text"]:hover,
input[type="text"]:active,
input[type="text"]:focus {
   border: 2px solid rgb(0, 125, 255);
   -webkit-box-shadow: inset 0.5px 0 2px rgb(0, 125, 255);
           box-shadow: inset 0.5px 0 2px rgb(0, 125, 255);
   height: 42px;
   font-size: 1.2rem;
}

input[type="submit"]:focus,
input[type="submit"]:hover,
input[type="submit"]:active {
  background: rgb(0, 125, 255);
  height: 48px;
}



/* ------------------- Navigation List ----------------------- */

#drawer-nav {
  height: 65vh;
  font-size: 1.1rem;
  background: rgb(17, 16, 16);
  -webkit-box-shadow: inset 1px 1px 10px #000;
  overflow-y: scroll;
  margin-top: 3px;
}

#drawer-nav ul {
  height: 65vh;
  list-style: none;
  padding: 0;
  margin: 0 .5rem 0 1.4rem
}

#drawer-nav p {
  display: none
}

#drawer-nav li {
  margin: 1rem 0;
  font-weight:400;
  font-family: Arial;
}

#drawer-nav a {
  color: white;
  text-shadow: 3px 3px rgba(0, 0, 0, 0.96);
  text-decoration: none;
  font-size: 1.1rem;
  cursor: pointer;
}

#marker-list a:hover,
#marker-list a:active,
#marker-list a:focus {
  color: rgba(0, 163, 255);
  outline: none;
}

#to-top {
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  background: transparent;
  color: rgb(96, 164, 236);
  border: none;
  padding-bottom: 10px;
  margin-top: 4px;
  margin-left: 60px;
  text-decoration: underline;
}

#to-top:hover
#to-top:active
#to-top:focus {
  color: white;
}


/* ----------------------Scroll Bar------------------------ */

#drawer-nav::-webkit-scrollbar {
    width: 1em;
}

#drawer-nav::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 5px 40px rgba(34, 34, 34, 0.85);
    -webkit-box-shadow: inset 1px 2px 6px #000;
    background: rgb(34, 34, 34);
}

#drawer-nav::-webkit-scrollbar-thumb {
  background-color: rgba(0, 125, 255, 0.75);
  -webkit-box-shadow: inset -1px -1px 3px #000;
  border-radius: 2px;
}

#drawer-nav::-webkit-scrollbar-thumb:hover,
#drawer-nav::-webkit-scrollbar-thumb:active {
  background-color: rgb(0, 125, 255);
}



