header {
  position: fixed;
  width: 100%;
  background: black;
  z-index: 999;
  color: white;
  height: 60px;
}

#logo, #logo a {
  color: white;
  text-decoration: none;
  font-size: 40px;
}

nav {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  z-index: 999;
}

#main-nav {
  -webkit-box-shadow: 2px 0px 5px rgb(23, 23, 23);
          box-shadow: 2px 0px 5px rgb(23, 23, 23);
}

#nav-list ul {
  list-style: none;
  margin: 0;
  padding: 0 20px;
  display: -ms-flexbox;
  display: flex;
}

#nav-list li {
  padding: 0 10px;
  font-size: 18px;
}

#nav-list a{
  color: white;
  text-decoration: none;
}

#nav-list a:hover,
#nav-list a:active {
  color: rgb(0, 232, 255);
}



