.drawer-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  text-align: center;
  height: 48px;
  width: 48px;
  background: transparent;
  border: none;
  cursor: pointer;
  margin: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
}

.button-line {
  width: 38px;
  height: 2px;
  background: white;
}

.drawer-button:hover  .button-line,
.drawer-button:active .button-line,
.drawer-button:focus .button-line {
  background: rgb(0, 163, 255);
}



